#graphism {
  width: 100vw;
  box-sizing: content-box;
  overflow: hidden;
  overflow-y: auto;
  z-index: 5;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 5vw);
  grid-gap: 15px;
  padding: 0 2rem; }
  @media (min-width: 1200px) {
    #graphism {
      columns: 4; } }
  @media (min-width: 900px) {
    #graphism {
      columns: 3; } }
  @media (min-width: 629px) {
    #graphism {
      columns: 2; } }
  @media (max-width: 628px) {
    #graphism {
      columns: 1; } }
  #graphism article.graphic {
    width: 100%;
    display: inline-block;
    overflow: hidden;
    position: relative;
    cursor: pointer; }
    #graphism article.graphic h2 {
      font-size: 2em;
      padding: 1rem;
      color: white;
      position: absolute;
      top: -100px;
      width: 100%;
      left: 0;
      transform: translateY(-50%);
      text-transform: uppercase;
      text-shadow: 1px 1px rgba(0, 0, 0, 0.8);
      text-align: center;
      transition: .3s ease; }
    #graphism article.graphic p.description {
      display: none; }
    #graphism article.graphic img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in; }
    #graphism article.graphic:nth-child(1) {
      grid-column: 1 / span 2;
      grid-row: 1 / span 2; }
    #graphism article.graphic:nth-child(2) {
      grid-column: 3 / span 2;
      grid-row: 1 / span 2; }
    #graphism article.graphic:nth-child(3) {
      grid-column: 5 / span 4;
      grid-row: 1 / span 5; }
    #graphism article.graphic:nth-child(4) {
      grid-column: 1 / span 4;
      grid-row: 3 / span 3; }
    #graphism article.graphic:nth-child(5) {
      grid-column: 1 / span 4;
      grid-row: 6 / span 3; }
    #graphism article.graphic:nth-child(6) {
      grid-column: 5 / span 4;
      grid-row: 6 / span 3; }
    #graphism article.graphic:hover h2 {
      top: 50%; }
    #graphism article.graphic:hover img {
      filter: blur(2px) brightness(0.1) hue-rotate(90deg); }

#gallery-fullscreen {
  position: fixed;
  height: calc(100% - 50px);
  width: 100%;
  overflow: hidden;
  background: rgba(15, 48, 87, 0.95);
  z-index: 10;
  top: 50px;
  left: 0; }
  #gallery-fullscreen section.selected img {
    width: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 80vw;
    max-height: 70vh;
    object-fit: contain; }
  #gallery-fullscreen section.selected header h3 {
    color: white;
    font-size: 2rem;
    text-shadow: 1px 2px 4px rgba(0, 0, 0, 0.8);
    text-transform: uppercase;
    text-align: center;
    width: 100%;
    position: absolute;
    top: 1rem; }
  #gallery-fullscreen section.selected p {
    display: none; }
  #gallery-fullscreen nav .nav {
    position: absolute;
    top: 50%;
    transform: translateY(-50%) translateZ(100px);
    cursor: pointer; }
    #gallery-fullscreen nav .nav .close svg {
      fill: white; }
    #gallery-fullscreen nav .nav.prev, #gallery-fullscreen nav .nav.next {
      color: white;
      width: 46px;
      height: 48px; }
    #gallery-fullscreen nav .nav.prev {
      left: 0; }
    #gallery-fullscreen nav .nav.next {
      right: 0;
      transform: translateY(-50%) rotateY(180deg); }
